import React from 'react';
import { Row, Col, Card, Table, Modal, Button } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Loader from '../../App/components/Reusables/Loader';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import { getPlaces, deletePlaces } from '../../api/inventory/placeApi';
import { getStartEnd } from '../../helpers/getStartEnd';

const Places = (props) => {
    const [placesArr, setPlacessArr] = React.useState([]);
    const [loading, setLoading] = React.useState(true)
    const [deleteData, setDeleteData] = React.useState({ showModal: false, placeId: null })
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {
        const controller = new AbortController();
        getPlaces({
            "action": "get",
            "filter": "none",
            ...getStartEnd(props)
        }, controller.signal)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    console.log(res.json.places)
                    setPlacessArr(res.json.places);
                    setInstanceCount(res.json.count)
                    setLoading(false);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err)
            });
        return () => controller.abort()
    }, []);



    const _triggerDeleteModal = (id) => {
        setDeleteData({ showModal: true, placeId: id });
    }

    const _delete = (idToBeDeleted) => {
        deletePlaces({ action: 'delete', places_id: [idToBeDeleted] })
            .then(async (res) => {
                console.log(res.json)
                if (res.json.status) {
                    const deletedPlace = placesArr.find(place => place.id === idToBeDeleted);
                    let tempArr = placesArr;
                    tempArr.splice(tempArr.indexOf(deletedPlace), 1);
                    await setPlacessArr([...tempArr])
                    await setDeleteData({ ...setDeleteData, showModal: false, placeId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Place deleted successfully!', background: '#dffff0' })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch((err) => {
                console.log(err)
            });
    }


    const PlacesList = placesArr.map((place, index) => {
        return (
            <tr key={index}>
                <th scope="row">{place.id}</th>
                <td
                    onClick={() => {
                        props.history.push('/place/' + place.id)
                    }}
                    style={{ cursor: 'pointer', color: '#55588b' }}>{place.name}</td>
                <td>{place.count_assignment}</td>
                <td>{place.is_active ? 'Yes' : 'No'}</td>
                <td>
                    <i
                        className="feather icon-edit text-c-green f-19 m-r-10"
                        style={{ cursor: 'pointer' }}
                        onClick={() => props.history.push('/place/update/' + place.id)}
                    />
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeleteModal(place.id)}
                    />
                </td>
            </tr>
        )
    })

    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Places</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`place/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>
            </Row>
            <Row>
                <Col>

                    <Card>
                        <Card.Body>
                            <Table striped responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Name</th>
                                        <th>No of Items Assigned</th>
                                        <th>Enabled</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {PlacesList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(placesArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>

                    </Card >


                    <DeleteModal
                        show={deleteData.showModal}
                        onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                        onDelete={() => _delete(deleteData.placeId)}
                    />


                </Col>
            </Row>
        </Aux>
    );
}

export default Places;